:root{
    --background-color: #FFFFFF;
    --valute-checked: #EEEEEE;
    --text: #1D1F22;
}
.cart-overlay-container > .cart-overlay{
    position: absolute;
    width: 325px;
    height: 677px;
    left: 1010px;
    background: var(--background-color);
    z-index: 2;
}
.product-overlay > .my-bag{
    display: flex;
    flex-direction: row;
    margin-top: 32px;
    margin-left: 16px;
}
.my-bag > div {
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    color: var(--text);
}
.product-overlay > .products-container{
    display: flex;
    flex-direction: column;
    margin-top: 32px;
    margin-left: 16px;

    width: 293px;
    height: 420px;
    overflow: auto;
}
.products-container > .cart-overlay-item-container{
    width:293px;
    margin-bottom: 20px;
}
.cart-overlay-item-container > .info-container-overlay{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.info-container-overlay > .cart-overlay-image-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.info-container-overlay > .product-data{
    width: 120px;
    margin-right: 12px;
}
.product-data > .name-overlay,
.product-data > .product-type-overlay{    
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 300;
    font-size: 13px;
    line-height: 160%;
    color: var(--text);
}
.product-data > .product-type-overlay{
    margin-top: 0;
}
.product-data > .price-overlay{
    margin-top: 4px;
    font-family: 'Raleway';
    font-size: 16px;
    line-height: 160%;
    text-align: left;

    color: var(--text);
}
.attributes > .txt-overlay{
    margin-top: 8px;
    font-family: 'Raleway';
    font-size: 14px;
    line-height: 16px;
    color: var(--text);
}
.choices > .size-overlay{
    min-width: 18px;
    height: 18px;
    margin-right: 8px;
    font-family: 'Source Sans Pro';
    font-size: 14px;
    padding-top: 4px;
    text-align: center;
    border: 1px solid rgba(0, 0, 0, 0.2);
    color: var(--text);
}
.choices > .color-overlay{
    width: 16px;
    height: 16px;
    margin-right: 8px;
    border: 1px solid rgba(0, 0, 0, 0.2);
}
.choices > .checked{
    background-color: var(--text);
    color: var(--background-color);
}
.info-container-overlay > .quantity-overlay{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.quantity-overlay > p{
    text-align: center;
}
.product-image-overlay > img{
    margin-left: 8px;
    width: 121px;
}
.product-overlay > .checkout{
    margin-top: 40px;
    margin-left: 16px;
    width: 293px;
}
.checkout > .total-price{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 32px;
}
.checkout > .checkout-btns{
    display: flex;
    flex-direction: row;
}
.checkout-btns > button{
    margin-right: 12px;
    width: 140px;
    height: 43px;
    background: var(--background-color);
    border: 1px solid var(--text);
}
a {
    text-decoration: none;
    color: var(--text);
}
#checkout{
    background: #5ECE7B;
    color: #FFFFFF;
}