:root{
    --background-color: #FFFFFF;
    --alert-background: #8D8F9A;
    --category-name: #1D1F22;
    --out-of-stock: #8D8F9A;
}
#root{
    position: absolute;
    width: 1440px;
    height: 1900px;
    margin: 0;
    background:var(--background-color);
}
#root > .app{
    width: 100%;
    height: 1900px;
}
.app > header{
    position:absolute;
    top: 0;
    padding: 24px 100px 0 100px;
    margin-bottom: 50px;
    width:100%;
    height: 80px;
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
    background-color: var(--background-color);
}
header > .left,
header > .right{
    display: flex;
    flex-direction: row;
    height: 29px;
}
header > .right{
    margin-left: 500px;
}
header > .center{
    width: 41px;
    height: 41px;
    margin-left: 350px;
}
.right > .cart-alert{
    width: 180px;
    height: 60px;
    margin-left: -180px;
    margin-top: 40px;
    border-radius: 5px;
    background-color:var(--alert-background);
}
.cart-alert > p{
    text-align: center;
    font-size: 20px;
    color:black;
}
header > .cart-overlay-background{
    display: flex;
    position: absolute;
    width: 1440px;
    height: 1800px;
    left: 0;
    top: 78px;
    background: rgba(57, 55, 72, 0.52);
    z-index: 1;
}
.left > .category{
    box-sizing: border-box;
    margin-right: 32px;
    height: 52px;
    display: flex;
}
.category > .text{
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    height: 20px;
}
.left > .clicked{
    border-bottom: 3px solid green;
}
.category > .text:hover{
    color: green;
}
.left > .category:hover{
    border-bottom: 3px solid green;
}
.right > .currency-selector{
    width:38px;
    height: 29px;
    margin-right: 22px;
}
.currency :hover{
    cursor: pointer;
}
.right > .convert{
    position: absolute;
    width: 114px;
    height: 260px;
    left: 1210px;
    top: 65px;
    padding-top: 17px;
    box-sizing:border-box;
    z-index: 1;
    background:#FFFFFF;
    filter: drop-shadow(0px 4px 35px rgba(168, 172, 176, 0.19));
}
.right :hover{
    cursor: pointer;
}
.right > .mini-cart{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
header > .hide{
    display: none;
}
.hide{
    display: none;
}
.show{
    display: flex;
}
.convert > .valute {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 114px;
    height: 45px;

    font-family: 'Raleway';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 160%;

    text-align: center;

    color: #1D1F22;
}
.convert > .checked{
    background: var(--valute-checked);
}
#valute{
    margin-right: 10px;
    margin-top: 5px;
    width: 12px;
}
#cart{
    width: 20px;
    height: 20px;
    margin-top: 5px;
}
.mini-cart > .cart-icon-container{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.cart-icon-container > .item-number{
    display: none;
    height: 20px;
    width: 20px;
    margin-bottom: -14px;
    margin-left: 10px;
    color: #EEEEEE;
    text-align: center;
    background-color: black;
    border-radius: 50%;
}
.cart-icon-container > .shown{
    display: inline-block;
}
.category-container > .category-name{
    width: 299px;
    height: 70px;
    margin-top: 100px;
    margin-left: 101px;

    font-family: 'Raleway';
    font-style: normal;
    font-size: 41px;
    display: flex;

    color: var(--category-name);
}
.category-container > .content{
    width:100%;
    height: 100%;
    margin-top: 50px;
    margin-left: 100px;
    display: grid;
    grid-template-columns: 386px 386px 386px;
    grid-template-rows: 444px 444px;
    column-gap: 40px;
    row-gap: 103px;
}
#product{
    width: 386px;
    height: 490px;
    background-color: var(--background-color);
}
#product:hover{
    transform: scale(1.1);
    filter: drop-shadow(0px 4px 35px rgba(168, 172, 176, 0.19));
}
.activatedFilter > .activatedCart{
    display: none;
}
#product > .deactivatedCart{
    display: none;
}
#product:hover 
.activatedCart{
    display: block;
    width: 52px;
    height: 52px;
    margin-left: 303px;
    margin-top: -79px;
    margin-bottom: 27px;
}
.activatedFilter > .activatedCart:hover{
    cursor: pointer;    
    filter: drop-shadow(0px 4px 11px rgba(29, 31, 34, 0.3));
}
#product > .product,
#product > .price{
    margin-top: 24px;
    margin-left: 16px;
    margin-bottom: 0px;
    
    font-family: 'Raleway';
    font-style: normal;
    font-size: 18px;
    line-height: 160%;
    color: var(--category-name);
}
#product > .price{
    width: 58px;
    margin-top: 0px;
    font-weight: 500;
    text-align: right;
}
#product > .product-view{
    width: 354px;
    height: 330px;
    margin: 16px;
}
.product-view > a > .product-img{
    width: 354px;
    height: 330px;
    object-fit:contain;
}
.product-view > a > .out-of-stock{
    opacity: 0.5;
}
.product-view > .stockStatus{
    margin-left: 90px;
    margin-top: -184px;

    font-family: 'Raleway';
    font-style: normal;
    font-size: 24px;
    text-transform: uppercase;

    color: var(--out-of-stock);
}