:root{
    --background-color: #FFFFFF;
    --valute-checked: #EEEEEE;
    --text: #1D1F22;
    --border: #E5E5E5;
    --button: #5ECE7B;
}
.app > .cart-page{
    width: 100%;
    margin: 0;
}
.cart-page > .cart{
    position: absolute;
    left: 100px;
    top: 110px;
    font-family: 'Raleway';
    font-size: 32px;
    text-transform: uppercase;
    color: var(--text);
}
.cart-page > .products{
    width: 1240px;
    margin-top: 175px;
    margin-left: 100px;
}
.products > .cart-container{
    width: 100%;
    min-height: 300px;
    max-height: 500px;
    border-bottom:1px solid #E5E5E5;;
}
.cart-container > .single-product{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    height: 90%;
    border-top:1px solid var(--border);
}
.single-product > .info-container{
    width:292px;
    margin: 24px;
    margin-left: 0;
}
.single-product > .quantity{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 24px;
    margin-left: 500px;
    width: 45px;
}
.info-container > .name{
    font-size: 30px;
    font-family: 'Raleway';
}
.info-container > .brand{
    font-family: 'Raleway';
}
.quantity > button{
    width: 45px;
    height: 45px;
    border: 1px solid var(--text);
    background: var(--background-color);
}
.quantity > button:hover{
    cursor: pointer;
}
.quantity > p{ 
    font-family: 'Raleway';
    font-style: normal;
    font-size: 24px;
    text-align: center;
    color: var(--text);
}
.info-container > .product-price{
    font-family: 'Raleway';
    font-style: normal;
    font-size: 35px;
    color: var(--text);
}
.single-product > .product-images{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 24px;
}
.product-images > img {
    max-width: 200px;
    max-height: 250px;
    object-fit: fill;
}
.product-images > .nav{
    position: absolute;
    margin-top: 215px;
    left: 1225px;
}
.product-images > .nav-hidden{
    display: none;
}
.nav > button {
    width: 24px;
    height: 24px;
    margin-left: 8px;

    background: rgba(0, 0, 0, 0.73);
    transform: matrix(-1, 0, 0, 1, 0, 0);
}
.cart-page > .place-order{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 32px;
    margin-left: 100px;
}
.place-order > .order-detail{
    display: flex;
    flex-direction: row;
    height: 28px;
}
.order-detail > p {
    margin: 0;
    width: 100px;
}
.order-detail > .total-number{
    font-weight: 700;
}
.place-order > .order-btn{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
    width: 279px;
    height: 43px;
    background: var(--button);
    color: var(--background-color);
    border: none;
}