:root{
    --background-color: #FFFFFF;
    --alert-background: #8D8F9A;
    --category-name: #1D1F22;
    --out-of-stock: #8D8F9A;
    --out-of-stock-text: #2B2B2B;
}
.app > .pdp-container{
    width: 100%;
    height: 100%;
    margin: 0;
}
.pdp-container > .pdp{
    margin-top: 80px;
    background-color:var(--background-color);
    display: flex;
    flex-direction: row;
}
.pdp > .photos{
    display: flex;
    flex-direction: column;
    width:80px;
    height:100%;
    margin-top: 80px;
    margin-left: 100px;
}
.single-photo > .product-image{
    box-sizing: border-box;
    object-fit:contain;
    width: 79px;
    height: 80px;
    margin-bottom: 32.39px;
}
.single-photo > .chosen{
    border: 3px solid #5ECE7B;;
}
.pdp > .description{
    display: flex;
    flex-direction: row;
    width: 1002px;
    height: 595px;
    margin-top: 80px;
    margin-left: 40px;
    background-color:var(--background-color);
}
.description > div > .product-img-chosen{
    width:610px;
}
.description > .info-pack{
    width: 300px;
    height: 100%;
    margin-left: 100px;
}
.info-pack > .name{
    width: 100%;
    height: 27px;
    font-family: 'Raleway';
    font-size: 30px;
    color: var(--category-name);
}
.info-pack > div > .txt,
.info-pack > .prc{
    height: 18px;
    margin-top: 43px;

    font-family: 'Roboto Condensed';
    font-weight: 700;
    font-size: 18px;
    line-height: 18px;

    display: flex;
    color: var(--category-name);
}
.info-pack > .prc{
    margin-top: 38px;
}
.info-pack > .prc-value{
    height: 46px;
    margin-top: 10px;

    font-family: 'Raleway';
    font-size: 24px;
    line-height: 18px;
    color: var(--category-name);
}
.choices{
    display: flex;
    flex-direction: row;

    height: 45px;
    margin-top: 8px;
}
.choices > .size{
    width: 63px;
    margin-right: 12px;

    border: 1px solid var(--category-name);
    box-sizing: border-box;
    font-family: 'Source Sans Pro';
    font-size: 16px;
    line-height: 18px;
    padding-top: 14px;
    text-align: center;
    letter-spacing: 0.05em;
    color: var(--category-name);
}
.choices > .size:hover,
.choices > .color:hover{
    cursor: pointer;
}
.choices > .checked-size{
    background-color: #1D1F22;
    color: #FFFFFF;
}
.choices > .color{
    box-sizing: border-box;
    width: 32px;
    height: 32px;
    margin-right: 10px;
    border: 1px solid rgba(0, 0, 0, 0.2)
}
.choices > .checked-clr{
    border: 1px solid #5ECE7B;
    transform: scale(1.2);
}
.info-pack > .add-to-cart,
.info-pack > .product-out-of-stock{
    margin-top: 20px;
    width: 292px;
    height: 52px;

    background: #5ECE7B;
    color: var(--background-color);
    border: none;
}
.info-pack > .product-out-of-stock{
    background: var(--out-of-stock);
    color: var(--out-of-stock-text);
}
.info-pack > .add-to-cart:hover{
    cursor:pointer;
    transform: scale(1.04);
}
.info-pack > .product-out-of-stock:hover{
    cursor:default;
}
.info-pack > .prod-description{
    bottom: 178px;
    margin-top: 40px;
    
    font-family: 'Roboto';
    font-size: 16px;
    line-height: 159.96%;
    color: var(--category-name);    
}